    padding-top: 35px;
}
.backdropStyle {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
 
  background-color: rgba(0,0,0,0.5);
}

.modalStyle {
  background: white;
  position: fixed;
  width: 50%;
  top: 50%;
  left: 50%;
  margin: -25% 0 0 -25%;

  z-index: 10; /* keep on top of other elements on the page */
    box-shadow: 0 0 0 9999px rgba(0,0,0,0.5);

}
